import React, { Component } from 'react'
import './Dropdown.css'
import { Icon } from 'antd'
import { connect } from 'react-redux'

class Dropdown extends Component {
  state = {
    stateOptions: []
  }

  componentDidMount() {
    this.setState({ stateOptions: [] })
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ stateOptions: [] })
  }

  toggleDropdown = () => {
    this.setState({ stateOptions: this.props.options })
    this.refs.ddl && this.refs.ddl.classList.toggle("show")
    this.refs.input && this.refs.input.focus()
  }

  filterFunction = () => {
    let input, filter, a, i
    input = this.refs.input
    filter = input.value.toUpperCase()
    let div = this.refs.ddl
    a = div.getElementsByTagName("li")
    for (i = 0; i < a.length; i++) {
      if (a[i].innerHTML.toUpperCase().indexOf(filter) > -1) {
        a[i].style.display = ""
      } else {
        a[i].style.display = "none"
      }
    }
  }

  onSelect = (data) => {
    if (this.props.onSelect) this.props.onSelect(data)
  }

  onBlur = (e) => {
    setTimeout(() => {
      this.refs.ddl && this.refs.ddl.classList.remove("show")
    }, 200)
  }

  render() {
    const {
      options,
      selected,
      placeholder,
      language
    } = this.props
    const { stateOptions } = this.state
    const option = options.filter(x => x.id === selected)[0]
    const searchText = language === 'th' ? 'ค้นหา' : 'search'
    return (
      <div className="custom-dropdown">
        <button onClick={this.toggleDropdown} className="dropbtn" title={option && option.text}>
          <span>
            {!option && placeholder}
            {option && option.text}
          </span>
        </button>
        <Icon className="icon" type="down" style={{ fontSize: '12px', color: '#66ccff' }} />
        <div ref="ddl" className="dropdown-content">
          <input type="text" placeholder={searchText} ref="input" onKeyUp={this.filterFunction} onBlur={this.onBlur} />
          <div className="list">
            {stateOptions.map((data, i) => {
              return <li key={i}
                onClick={this.onSelect.bind(null, data)}>{data.text}</li>
            })}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(Dropdown)
