import React, { Component } from 'react'
import {
  Button,
  Upload,
  Icon,
  message
} from 'antd'

class UploadFile extends Component {
  state = {
    defaultFileList: []
  }

  onChange = (info) => {
    if (info.file.status !== 'uploading') {
      // console.log(info.file, info.fileList)
    }
    if (info.file.status === 'done') {
      message.success(`${info.file.name} file uploaded successfully`)
    } else if (info.file.status === 'error') {
      message.error(`${info.file.name} file upload failed.`)
    }
  }

  render() {
    const { isNoDownload } = this.props
    const { defaultFileList } = this.state
    const props = {
      name: 'file',
      action: '//jsonplaceholder.typicode.com/posts/',
      headers: {
        authorization: 'authorization-text'
      }, defaultFileList
    }
    return (
      <div className="input-group">
        <div className="input-group-prepend">
          <Upload {...props} onChange={this.onChange} style={{ width: '100%' }}>
            <Button block className="btn-success"><Icon type="plus" />Add File</Button>
          </Upload>
          {!isNoDownload && <Button block className="btn-primary ml-1"><Icon type="cloud-download" />Add File</Button>}
        </div>
      </div>
    );
  }
}

export default UploadFile