import React, { Component } from 'react'
import moment from 'moment'
import localeTH from 'antd/lib/date-picker/locale/th_TH'
import localeEN from 'antd/lib/date-picker/locale/en_US'
import { Select, DatePicker, TimePicker, Checkbox, Input } from 'antd'
import CustomDropdown from '../../components/Dropdown'
import './AdvanceSearch.css'
import context from '../../utils/language'
import { connect } from 'react-redux'

const { RangePicker } = DatePicker
const timeFormat = 'HH:mm'

class AdvanceSearch extends Component {
  render() {
    const { show, dropdown, form, language } = this.props
    const name = context[language].search
    const options = dropdown.airTypeOptions.map(x => {
      x.id = x.airTypeID
      x.text = x.airType
      return x
    })
    const optionsList = [
      { label: (name && name.arrival), value: '1' },
      { label: (name && name.departure), value: '2' }
    ]
    const style = show ? 'show' : 'hide'
    return (
      <div className={"adv-search-container " + style}>
        <h5 className="text-secondary text-center p-2">{name && name.advanceSearch}</h5>
        <div className="row">
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.betweenDate} :</label>
            <div className="col col-xl-8">
              <RangePicker
                name="rangeDate"
                onChange={(value, dateString) => {
                  if (this.props.onChange) this.props.onChange('startDate', dateString[0])
                  if (this.props.onChange) this.props.onChange('endDate', dateString[1])
                }}
                locale={language === 'th' ? localeTH : localeEN}
                value={[
                  form.startDate ? moment(form.startDate) : null,
                  form.endDate ? moment(form.endDate) : null,
                ]}
                style={{ width: '100%' }}
              />
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.betweenTime} :</label>
            <div className="col col-xl-8">
              <div className="form-row">
                <div className="col">
                  <TimePicker
                    name="startTime"
                    onChange={(value, dateString) => {
                      if (this.props.onChange) this.props.onChange('startTime', dateString)
                    }}
                    defaultOpenValue={moment(form.startTime || '00:00', timeFormat)}
                    format={timeFormat}
                    placeholder={name && name.betweenDatePlaceholder}
                    style={{ width: '100%' }}
                  />
                </div>
                <div className="col-auto align-self-center">{' '}-{' '}</div>
                <div className="col">
                  <TimePicker
                    name="endTime"
                    onChange={(value, dateString) => {
                      if (this.props.onChange) this.props.onChange('endTime', dateString)
                    }}
                    defaultOpenValue={moment(form.endTime || '00:00', timeFormat)}
                    format={timeFormat}
                    placeholder={name && name.betweenTimePlaceholder}
                    style={{ width: '100%' }}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.callsign} :</label>
            <div className="col col-xl-8">
              <Select
                name="callsign"
                onChange={(value, dateString) => {
                  if (this.props.onChange) this.props.onChange('callsign', value)
                }}
                mode="multiple"
                style={{ width: '100%' }}
                placeholder={name && name.callsignPlaceholder}>
                {dropdown.traffTypeOptions.map((t,i)=>(
                  <Select.Option key={t.value}>{t.value}</Select.Option>
                ))}
              </Select>
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.airType} :</label>
            <div className="col col-xl-8">
              <CustomDropdown
                selected={form.airTypeID}
                onSelect={item => {
                  if (this.props.onChange) this.props.onChange('airTypeID', item.airTypeID)
                }}
                placeholder={name && name.airTypePlaceholder}
                options={options} />
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.trafftype} :</label>
            <div className="col col-xl-8">
              <Select
                name="trafftype"
                onChange={(value) => {
                  if (this.props.onChange) this.props.onChange('traffType', value)
                }}
                mode="multiple"
                style={{ width: '100%' }}
                placeholder={name && name.trafftypePlaceholder}>
                {dropdown.traffTypeOptions.map((t,i)=>(
                  <Select.Option key={t.value}>{t.value}</Select.Option>
                ))}
              </Select>
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.destination} :</label>
            <div className="col col-xl-8">
              <Select
                name="destination"
                onChange={(value) => {
                  if (this.props.onChange) this.props.onChange('destination', value)
                }}
                mode="multiple"
                style={{ width: '100%' }}
                placeholder={name && name.destinationPlaceholder}>
                {dropdown.traffTypeOptions.map((t,i)=>(
                  <Select.Option key={t.value}>{t.value}</Select.Option>
                ))}
              </Select>
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.checkInOut} :</label>
            <div className="col col-xl-8">
              <Checkbox
                name="checkInOutAll"
                indeterminate={form.checkInOut && form.checkInOut.length === 1}
                onChange={e => {
                  if (this.props.onChange) this.props.onChange('checkInOut', e.target.checked ? ['1', '2'] : [])
                }}
                checked={form.checkInOut && form.checkInOut.length === 2}
                className="d-inline-block mr-2 border-right">{name && name.all}</Checkbox>
              <Checkbox.Group
                name="checkInOut"
                options={optionsList}
                onChange={checkedList => {
                  if (this.props.onChange) this.props.onChange('checkInOut', checkedList)
                }}
                value={form.checkInOut || []}
              />
            </div>
          </div>
          <div className="col-12 col-lg-6 form-inline form-row mb-2">
            <label className="col-12 col-sm-4 justify-content-end">{name && name.cardNo} :</label>
            <div className="col col-xl-8">
              <Input
                name="cardNo"
                onChange={e => {
                  if (this.props.onChange) this.props.onChange('cardNo', e.target.value)
                }}
                value={form.cardNo || ''}
                placeholder={name && name.cardNoPlaceholder}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

export default connect(mapStateToProps)(AdvanceSearch)
