import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux'
import context from '../../utils/language'
import {
  Button,
  Input,
  Popover,
  DatePicker,
  Radio,
  Select,
  Icon
} from 'antd'
import moment from 'moment'
import TextArea from 'antd/lib/input/TextArea'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ModalDetailHistory from './ModalDetailHistory'
import UploadFile from './UploadFile'

class ModalDetail extends Component {
  state = {
    form: {},
    isShowHistory: false
  }

  onChange = (e) => {
    const { name, value } = e.target
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  renderDatepicker = (data, index) => {
    return <div key={index} className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
      <label className="col-12 col-sm-6 justify-content-end col-form-label">{data.label} :</label>
      <div className="col col-sm-6">
        <DatePicker
          style={{ width: '100%' }}
          name={data.name}
          placeholder={data.placeholder}
          onChange={(date, dateString) => this.onChange({ target: { name: data.name, value: dateString } })}
          value={data.value ? moment(data.value) : null} // with format use moment(data.value, 'YYYYMMDD')
        />
      </div>
    </div>
  }

  renderInput = (data, index) => {
    return <div key={index} className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
      <label className="col-12 col-sm-6 justify-content-end col-form-label">{data.label} : </label>
      <div className="col col-sm-6">
        <Input
          disabled={data.disabled}
          addonAfter={
            data.addon && <Popover content={data.addon.detail} title={data.addon.title}>
              <FontAwesomeIcon icon={data.addon.icon} />
            </Popover>
          }
          onChange={this.onChange}
          name={data.name}
          placeholder={data.placeholder}
          value={data.value}
          style={{ width: '100%' }}
        />
      </div>
    </div>
  }

  renderSelect = (data, index) => {
    return <div key={index} className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
      <label className="col-12 col-sm-6 justify-content-end col-form-label">{data.label} :</label>
      <div className="col col-sm-6">
        <Select
          name={data.name}
          disabled={data.disabled}
          showSearch
          style={{ width: '100%' }}
          placeholder={data.placeholder}
          optionFilterProp="children"
          onChange={value => this.onChange({ target: { name: data.name, value: value } })}
          filterOption={(input, option) =>
            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          value={data.value}>
          {data.options.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
        </Select>
      </div>
    </div>
  }

  renderTextArea = (data, index) => {
    return <div key={index} className="col-12 col-md-12 col-xl-12 form-inline form-row mb-2">
      <label className="col-12 col-sm-6 col-xl-2 justify-content-end col-form-label">{data.label} :</label>
      <div className="col col-sm-6 col-xl-10 pr-xl-4">
        <TextArea
          placeholder={data.placeholder}
          rows="3"
          name={data.name}
          onChange={this.onChange}
          value={data.value}
          style={{ width: '100%' }}
        />
      </div>
    </div>
  }

  renderTemplate = (template, i) => {
    if (template.type === 'input') return this.renderInput(template, i)
    if (template.type === 'datepicker') return this.renderDatepicker(template, i)
    if (template.type === 'select') return this.renderSelect(template, i)
    if (template.type === 'textarea') return this.renderTextArea(template, i)
  }

  toggleHistory = () => this.setState({ isShowHistory: !this.state.isShowHistory })

  render() {
    const { language, selectedFlight, dropdown } = this.props
    const { form, isShowHistory } = this.state
    let data = { ...selectedFlight, ...form }
    const name = context[language].viewStatModal

    const optionsMock = [
      { value: 'Owner Mock 1', text: 'Owner Mock 1' },
      { value: 'Owner Mock 2', text: 'Owner Mock 2' }
    ]

    const optionsAG = [
      { value: 'A', text: 'A' },
      { value: 'B', text: 'B' },
      { value: 'C', text: 'C' },
      { value: 'D', text: 'D' },
      { value: 'E', text: 'E' },
      { value: 'F', text: 'F' },
      { value: 'G', text: 'G' }
    ]

    let uniqueIATA = []
    for (let item of dropdown.airPortOptions) {
      if (uniqueIATA.indexOf(item.airportCodeIATA) === -1) uniqueIATA.push(item.airportCodeIATA)
    }

    const flightDetail = [
      {
        type: 'datepicker',
        name: 'trnDate',
        label: name && name.trnDate,
        value: data.trnDate,
        placeholder: name && name.trnDatePlaceholder,
      }, {
        type: 'input',
        name: 'trnTime',
        label: name && name.trnTime,
        value: data.trnTime,
        placeholder: name && name.trnTimePlaceholder,
      }, {
        type: 'select',
        name: 'trnDay',
        disabled: true,
        label: name && name.trnDay,
        value: data.trnDay,
        placeholder: name && name.trnDayPlaceholder,
        options: [
          { value: '1', text: language === 'th' ? 'จันทร์' : 'Monday' },
          { value: '2', text: language === 'th' ? 'อังคาร' : 'Tuesday' },
          { value: '3', text: language === 'th' ? 'พุธ' : 'Wednesday' },
          { value: '4', text: language === 'th' ? 'พฤหัสบดี' : 'Thursday' },
          { value: '5', text: language === 'th' ? 'ศุกร์' : 'Friday' },
          { value: '6', text: language === 'th' ? 'เสาร์' : 'Saturday' },
          { value: '7', text: language === 'th' ? 'อาทิตย์' : 'Sunday' },
        ]
      }, {
        type: 'select',
        name: 'airline',
        label: name && name.airline,
        value: data.airline,
        placeholder: name && name.airlinePlaceholder,
        options: [
          { value: 'CMI', text: 'CMI' },
          { value: 'ATT', text: 'ATT' },
          { value: 'DD', text: 'DD' },
          { value: 'SIA', text: 'SIA' },
        ]
      }, {
        type: 'select',
        name: 'callsign',
        label: name && name.callsign,
        value: data.callsign,
        placeholder: name && name.callsignPlaceholder,
        options: [
          { value: 'GLA-644', text: 'GLA-644' },
          { value: 'HS-LUW', text: 'HS-LUW' },
          { value: 'B-1610', text: 'B-1610' },
          { value: 'SNY-15', text: 'SNY-15' },
        ]
      }, {
        type: 'select',
        name: 'airType',
        label: name && name.airtype,
        value: data.airType,
        placeholder: name && name.airtypePlaceholder,
        options: [
          { value: 'EC-45', text: 'EC-45' },
          { value: 'B738', text: 'B738' },
          { value: 'A320', text: 'A320' },
          { value: 'DA-42', text: 'DA-42' },
        ]
      }, {
        type: 'input',
        name: 'grossWeight',
        label: name && name.grossWeight,
        value: data.grossWeight,
        placeholder: name && name.grossWeightPlaceholder,
        addon: {
          title: name && name.grossWeight,
          detail: name && name.grossWeightDesc,
          icon: 'info'
        }
      }, {
        type: 'select',
        name: 'owner',
        label: name && name.owner,
        value: data.owner,
        placeholder: name && name.ownerPlaceholder,
        options: optionsMock
      }, {
        type: 'input',
        name: 'agent',
        label: name && name.agent,
        value: data.agent,
        placeholder: name && name.agentPlaceholder,
        addon: {
          title: name && name.agent,
          detail: name && name.agentDesc,
          icon: 'info'
        }
      }, {
        type: 'input',
        name: 'flightNo',
        label: name && name.flightNo,
        value: data.flightNo,
        placeholder: name && name.flightNoPlaceholder,
      }, {
        type: 'input',
        name: 'scheduleTime',
        label: name && name.scheduleTime,
        value: data.scheduleTime,
        placeholder: name && name.scheduleTimePlaceholder,
      }, {
        type: 'select',
        name: 'arr_dep',
        label: name && name.arrDep,
        value: data.arr_dep,
        placeholder: name && name.arrDepPlaceholder,
        options: [
          { value: 'A', text: name && name.arrival },
          { value: 'D', text: name && name.departure }
        ]
      }, {
        type: 'select',
        name: 'portNameICAO',
        label: name && name.portNameICAO,
        value: data.portNameICAO,
        placeholder: name && name.portNameICAOPlaceholder,
        options: dropdown.airPortOptions.map(x => ({ value: x.airportCodeICAO, text: x.airportCodeICAO }))
      }, {
        type: 'select',
        name: 'portNameIATA',
        label: name && name.portNameIATA,
        value: data.portNameIATA,
        placeholder: name && name.portNameIATAPlaceholder,
        options: uniqueIATA.map(x => ({ value: x, text: x }))
      }, {
        type: 'select',
        name: 'traffType',
        label: name && name.traffType,
        value: data.traffType,
        placeholder: name && name.traffTypePlaceholder,
        options: optionsAG
      }, {
        type: 'select',
        name: 'portType',
        disabled: true,
        label: name && name.portType,
        value: data.portType,
        placeholder: name && name.portTypePlaceholder,
        options: optionsAG
      }, {
        type: 'select',
        name: 'cityICAO',
        label: name && name.city,
        value: data.cityICAO,
        placeholder: name && name.cityPlaceholder,
        options: optionsMock
      }, {
        type: 'select',
        name: 'cityIATA',
        label: name && name.cityIATA,
        value: data.cityIATA,
        placeholder: name && name.cityIATAPlaceholder,
        options: optionsMock
      }, {
        type: 'input',
        name: 'cardNo',
        label: name && name.cardNo,
        value: data.cardNo,
        placeholder: name && name.cardNoPlaceholder,
      }, {
        type: 'textarea',
        name: 'airportRemark',
        label: name && name.airportRemark,
        value: data.airportRemark,
        placeholder: name && name.airportRemarkPlaceholder,
      }, {
        type: 'input',
        name: 'zoneNo',
        disabled: true,
        label: name && name.zoneNo,
        value: data.zoneNo,
        placeholder: name && name.zoneNoPlaceholder,
      }, {
        type: 'input',
        disabled: true,
        name: 'regionNo',
        label: name && name.regionNo,
        value: data.regionNo,
        placeholder: name && name.regionNoPlaceholder,
      }
    ]

    const flightStatus = [
      {
        type: 'select',
        name: 'flightStatsID',
        label: name && name.flightStatus,
        value: data.flightStatsID,
        placeholder: name && name.flightStatusPlaceholder,
        options: dropdown.flightStatusOptions.map(x => ({ value: x.configFlightStatusId, text: x.configFlightStatusName }))
      }, {
        type: 'input',
        name: 'flightStatusTime',
        label: name && name.time,
        value: data.flightStatusTime,
        placeholder: name && name.timePlaceholder,
      }, {
        type: 'input',
        name: 'flightStatusRemark',
        label: name && name.flightStatusRemark,
        value: data.flightStatusRemark,
        placeholder: name && name.flightStatusRemarkPlaceholder,
      }
    ]

    const generalDeclaration = [
      {
        type: 'input',
        name: 'captain',
        label: name && name.captain,
        value: data.captain,
        placeholder: name && name.captainPlaceholder,
      },
      {
        type: 'input',
        name: 'crew',
        label: name && name.crew,
        value: data.crew,
        placeholder: name && name.crewPlaceholder,
      }
    ]

    const numberPassenger = [
      {
        type: 'input',
        name: 'passenger_total',
        disabled: true,
        label: name && name.passenger,
        value: data.passenger_total,
        placeholder: name && name.passengerPlaceholder,
      }, {
        type: 'input',
        name: 'passenger_inter',
        label: name && name.internation,
        value: data.passenger_inter,
        placeholder: name && name.internationPlaceholder,
      }, {
        type: 'input',
        name: 'passenger_dom',
        label: name && name.domestic,
        value: data.passenger_dom,
        placeholder: name && name.domesticPlaceholder,
      }, {
        type: 'input',
        name: 'passenger_infants',
        label: name && name.infants,
        value: data.passenger_infants,
        placeholder: name && name.infantsPlaceholder,
      }, {
        type: 'input',
        name: 'passenger_transfer',
        label: name && name.transfer,
        value: data.passenger_transfer,
        placeholder: name && name.transferPlaceholder,
      }, {
        type: 'input',
        name: 'passenger_transit',
        label: name && name.transit,
        value: data.passenger_transit,
        placeholder: name && name.transitPlaceholder,
      }
    ]

    const weightCargo = [
      {
        type: 'input',
        name: 'freight_total',
        disabled: true,
        label: name && name.freight,
        value: data.freight_total,
        placeholder: name && name.freightPlaceholder,
      }, {
        type: 'input',
        name: 'freight_inter',
        label: name && name.freightInternation,
        value: data.freight_inter,
        placeholder: name && name.freightInternationPlaceholder,
      }, {
        type: 'input',
        name: 'freight_dom',
        label: name && name.freightDomestic,
        value: data.freight_dom,
        placeholder: name && name.freightDomesticPlaceholder,
      }, {
        type: 'input',
        name: 'freight_transfer',
        label: name && name.freightTransfer,
        value: data.freight_transfer,
        placeholder: name && name.freightTransferPlaceholder,
      }, {
        type: 'input',
        name: 'freight_transit',
        label: name && name.freightTransit,
        value: data.freight_transit,
        placeholder: name && name.freightTransitPlaceholder,
      }
    ]

    const weightMail = [
      {
        type: 'input',
        name: 'mail_inbound',
        label: name && name.mailInbound,
        value: data.mail_inbound,
        placeholder: name && name.mailInboundPlaceholder,
      }, {
        type: 'input',
        name: 'mail_transit',
        label: name && name.mailTransit,
        value: data.mail_transit,
        placeholder: name && name.mailTransitPlaceholder,
      }
    ]

    const remark = {
      type: 'textarea',
      name: 'remark',
      label: name && name.remark,
      value: data.remark,
      placeholder: name && name.remarkPlaceholder,
    }

    return (
      <Fragment>
        {/* Flight Detail */}
        <h5 className="mb-4">{name && name.flightDetail}</h5>
        <div className="form-row mb-4">
          {flightDetail.map((template, i) => this.renderTemplate(template, i))}
        </div>

        {/* Flight Status */}
        <h5 className="mb-4">{name && name.updateFlightStatus}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            {flightStatus.map((template, i) => this.renderTemplate(template, i))}
          </div>
          <div className="form-row">
            <Button type="primary" onClick={this.toggleHistory}>
              Show All Flight Status<Icon type={isShowHistory ? 'up' : 'down'} />
            </Button>
          </div>
          {isShowHistory && <div className="form-row">
            <ModalDetailHistory />
          </div>}
        </div>

        {/* Pit Parking */}
        <div className="col-12 col-md-6 col-xl-8 form-inline form-row mb-2">
          <label className="col-12 col-sm-6 col-xl-3 justify-content-end col-form-label">{name && name.pitParking} :</label>
          <div className="col col-sm col-xl">
            <Radio.Group
              name={'pitParking'}
              onChange={this.onChange}
              value={data.pitParking}>
              <Radio value={1}>{name && name.unknown}</Radio>
              <Radio value={2}>{name && name.pit}</Radio>
              <Radio value={3}>{name && name.parking}</Radio>
            </Radio.Group>
          </div>
          {(data.pitParking === 2 || data.pitParking === 3) && <div className="col col-sm col-xl">
            <Select
              name={'selectPitParking'}
              showSearch
              style={{ width: '100%' }}
              placeholder={name && name.selectPitParkingPlaceholder}
              optionFilterProp="children"
              onChange={value => this.onChange({ target: { name: 'selectPitParking', value: data.selectPitParking } })}
              filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
              value={data.selectPitParking}>
              {optionsAG.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
            </Select>
          </div>}
        </div>

        {/* General Declaration */}
        <h5 className="mb-4">{name && name.generalDeclaration}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            {generalDeclaration.map((template, i) => this.renderTemplate(template, i))}
          </div>
          <div className="form-row">
            <UploadFile />
          </div>
        </div>

        {/* Number of Passenger */}
        <h5 className="mb-4">{name && name.passengerTitle}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            {numberPassenger.map((template, i) => this.renderTemplate(template, i))}
          </div>
          <div className="form-row">
            <UploadFile />
          </div>
        </div>

        {/* Weight of Cargo */}
        <h5 className="mb-4">{name && name.weightTitle}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            {weightCargo.map((template, i) => this.renderTemplate(template, i))}
          </div>
          <div className="form-row">
            <UploadFile />
          </div>
        </div>

        {/* Weight of Mail */}
        <h5 className="mb-4">{name && name.mailTitle}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            {weightMail.map((template, i) => this.renderTemplate(template, i))}
          </div>
        </div>

        {/* Remark */}
        <div className="form-row mb-4">
          {this.renderTextArea(remark, 1)}
        </div>

        {/* Other */}
        <h5 className="mb-4">{name && name.otherTitle}</h5>
        <div className="card card-body bg-info-light mb-4">
          <div className="form-row">
            <UploadFile isNoDownload />
          </div>
        </div>

        {/* footer */}
        <div className="text-center">
          <button
            onClick={this.props.onClose}
            className="btn btn-light mr-2">
            {name && name.cancel}
          </button>
          <button
            onClick={this.props.onClose}
            className="btn btn-danger mr-2">
            {name && name.reject}
          </button>
          <button
            onClick={this.props.onClose}
            className="btn btn-success mr-2">
            {name && name.save}
          </button>
          <button
            onClick={this.props.onClose}
            className="btn btn-info mr-2">
            {name && name.approve}
          </button>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
  selectedFlight: state.selectedFlight,
  dropdown: state.dropdown
})

export default connect(mapStateToProps)(ModalDetail)
