import React, { Component } from 'react'
import { Divider } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import FlightDetailTable from './FlightDetailTable'

class FlightDetail extends Component {
  render() {
    return (
      <div className="card card-body card-body-component">
        <div className="row mb-2">
          <div className="col-auto">
            <Link to="/flightschedule">
              <FontAwesomeIcon icon="angle-double-left" /><span className="ml-2">Back</span>
            </Link>
          </div>
        </div>

        <div className="card card-body bg-light border-0 mb-2">
          <div className="container-fluid">
            <div className="form-inline">
              <label className="col-lg-2 col-md-3 col-sm-4 justify-content-end">Traff Type :</label>
              <label className="col-lg-10 col-md-9 col-sm-8  justify-content-start">C - International Flight</label>
            </div>
          </div>
          <Divider />
          <div className="container-fluid">
            <div className="form-inline">
              <label className="col-lg-2 col-md-3 col-sm-4 justify-content-end">Date :</label>
              <label className="col-lg-10 col-md-9 col-sm-8  justify-content-start">1 August 2018 - 31 August 2018</label>
            </div>
          </div>
          <div className="container-fluid">
            <div className="form-inline">
              <label className="col-lg-2 col-md-3 col-sm-4 justify-content-end"></label>
              <label className="col-lg-10 col-md-9 col-sm-8  justify-content-start mt-3">Monday, Tuesday, Friday</label>
            </div>
          </div>
          <Divider />
          <div className="container-fluid">
            <div className="form-inline">
              <label className="col-lg-2 col-md-3 col-sm-4 justify-content-end">Update Date :</label>
              <label className="col-lg-6 col-md-4 col-sm-3  justify-content-start">5 January 2018</label>
              <label className="col-lg-4 col-md-5 col-sm-5  justify-content-end">Update by : ศตวรรษ โชคดี</label>
            </div>
          </div>
        </div>
      
        <FlightDetailTable {...this.props} />
      </div>
    );
  }
}

export default FlightDetail