import React from 'react'
import {
  Input,
  Popover,
  DatePicker,
  Select,
} from 'antd'
import moment from 'moment'
import TextArea from 'antd/lib/input/TextArea'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const renderDatepicker = (data, onChange) => {
  return <DatePicker
    style={{ width: '100%' }}
    name={data.name}
    placeholder={data.placeholder}
    onChange={(date, dateString) => onChange({ name: data.name, value: dateString })}
    value={data.value ? moment(data.value) : null} // with format use moment(data.value, 'YYYYMMDD')
  />
}

const renderInput = (data, onChange) => {
  return <Input
    disabled={data.disabled}
    addonAfter={
      data.addon && <Popover content={data.addon.detail} title={data.addon.title}>
        <FontAwesomeIcon icon={data.addon.icon} />
      </Popover>
    }
    onChange={e => onChange({ name: data.name, value: e.target.value })}
    name={data.name}
    placeholder={data.placeholder}
    value={data.value}
    style={{ width: '100%' }}
  />
}

const renderSelect = (data, onChange) => {
  return <Select
    name={data.name}
    disabled={data.disabled}
    showSearch
    mode={data.mode}
    style={{ width: '100%' }}
    placeholder={data.placeholder}
    optionFilterProp="children"
    onChange={value => onChange({ name: data.name, value: value })}
    filterOption={(input, option) =>
      option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
    }
    value={data.value}>
    {data.options.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
  </Select>
}

const renderTextArea = (data, onChange) => {
  return <TextArea
    placeholder={data.placeholder}
    rows="3"
    name={data.name}
    onChange={e => onChange({ name: data.name, value: e.target.value })}
    value={data.value}
    style={{ width: '100%' }}
  />
}

export default (template, onChange) => {
  if (template.type === 'input') return renderInput(template, onChange)
  if (template.type === 'datepicker') return renderDatepicker(template, onChange)
  if (template.type === 'select') return renderSelect(template, onChange)
  if (template.type === 'textarea') return renderTextArea(template, onChange)
  return template.component
}
