import React, { Component } from 'react'
import { Modal } from 'antd'
import ModalDetail from './ModalDetail'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import context from '../../utils/language'

class ModalComponent extends Component {
  render() {
    const visible = this.props.visible
    const dataStatus = this.props.data ? this.props.data.status : 1
    const { language } = this.props
    const name = context[language].viewStatModal
    return (
      <Modal
        title={name && name.title}
        visible={visible}
        width="90%"
        onCancel={this.props.onClose}
        destroyOnClose={true}
        footer={null}>

        <div className="d-flex justify-content-end">
          {dataStatus === 1 && <div className="alert alert-warning px-3 py-2">
            <h5 className="mb-0">
              <FontAwesomeIcon icon="clock" /> {name && name.waitingApprove}
            </h5>
          </div>}

          {dataStatus === 3 && <div className="alert alert-danger px-3 py-2">
            <h5 className="mb-0">
              <FontAwesomeIcon icon="exclamation-triangle" /> {name && name.waitingEdit}
            </h5>
          </div>}
          {dataStatus === 2 && <div className="alert alert-success px-3 py-2">
            <h5 className="mb-0">
              <FontAwesomeIcon icon="check" /> {name && name.approved}
            </h5>
          </div>}

          {dataStatus === undefined && <div className="alert alert-info">
            <h5 className="mb-0">
              <FontAwesomeIcon icon="file-alt" /> {name && name.draft}
            </h5>
          </div>}
        </div>

        <ModalDetail onClose={this.props.onClose} />
      </Modal>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

export default connect(mapStateToProps)(ModalComponent)