import React, { Component } from 'react'
import { Modal, Button, Select, Radio } from 'antd'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Render from '../../components/Renderer'
import context from '../../utils/language'

class ModalStatus extends Component {
  state = {
    form: {}
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  render() {
    const { open, flight, dropdown, language, selectedFlight } = this.props
    const { form } = this.state
    const name = context[language].viewStatModal
    const data = { ...flight, ...form }
    const template = [
      {
        type: 'input',
        isFull: true,
        name: 'codeShare',
        label: 'Code share',
        value: data.codeShare,
        placeholder: name && name.codeSharePlaceholder,
      }, {
        type: 'select',
        name: 'flightStatsID',
        label: name && name.flightStatus,
        value: data.flightStatsID,
        placeholder: name && name.flightStatusPlaceholder,
        options: dropdown
          .flightStatusOptions
          .filter(x => x.arrivalDepartureStatus === (selectedFlight.type === "arrival" ? 'A' : 'D'))
          .map(x => ({ value: x.configFlightStatusId, text: x.configFlightStatusName }))
      }, {
        type: 'input',
        name: 'flightStatusTime',
        label: name && name.time,
        value: data.flightStatusTime,
        placeholder: name && name.timePlaceholder,
      }, {
        type: 'select',
        name: 'cause',
        isFull: true,
        label: name && name.cause,
        value: data.cause,
        placeholder: name && name.causePlaceholder,
        options: [

        ]
      }, {
        type: 'select',
        name: 'callsign',
        label: name && name.callsign,
        value: data.callsign,
        placeholder: name && name.callsignPlaceholder,
        options: [
          { value: 'GLA-644', text: 'GLA-644' },
          { value: 'HS-LUW', text: 'HS-LUW' },
          { value: 'B-1610', text: 'B-1610' },
          { value: 'SNY-15', text: 'SNY-15' },
        ]
      }, {
        type: 'select',
        name: 'airType',
        label: name && name.airtype,
        value: data.airType,
        placeholder: name && name.airtypePlaceholder,
        options: [
          { value: 'EC-45', text: 'EC-45' },
          { value: 'B738', text: 'B738' },
          { value: 'A320', text: 'A320' },
          { value: 'DA-42', text: 'DA-42' },
        ]
      }, {
        type: 'input',
        name: 'terminal',
        label: 'Terminal',
        value: data.terminal,
        placeholder: name && name.terminalPlaceholder,
      }, {
        type: 'input',
        name: 'belt',
        label: 'Belt',
        value: data.belt,
        placeholder: name && name.beltPlaceholder,
      }
    ]

    const remark = {
      type: 'input',
      name: 'flightRemark',
      label: 'Flight Remark',
      value: data.flightRemark,
      placeholder: name && name.beltPlaceholder,
    }

    return (
      <Modal
        title={'Update Flight Status'}
        visible={open}
        width="850px"
        onCancel={this.props.onClose}
        destroyOnClose={true}
        footer={null}>
        <Button type="primary" shape="circle">
          <FontAwesomeIcon icon={flight.type === 'arrival' ? "plane-arrival" : 'plane-departure'} />
        </Button>
        <label className="ml-2"><h5>{flight.type === 'arrival' ? 'Arrival' : 'Departure'}</h5></label>

        <div className="card card-body bg-primary-light mb-4 col-sm-12">
          <div className="form-row">
            <div className="col col-sm-4">
              <div>Flight No</div>
              <h5 className="mb-0">DD 7910</h5>
              <div>Nok Air</div>
            </div>
            <div className="col col-sm-4">
              <div>From</div>
              <h5 className="mb-0">Don Mueang (DMK) <FontAwesomeIcon className="fa-lg pt-1 pl-2" icon="angle-double-right" /></h5>
              <div>03:55</div>
            </div>
            <div className="col col-sm-4">
              <div>To</div>
              <h5 className="mb-0">Suratthani (URT)</h5>
              <div>04:25</div>
            </div>
          </div>
        </div>

        <div className="form-row">
          {template.map((t, i) => (
            <div key={i} className={t.isFull ? "col-sm-12 form-inline mb-2" : "col-sm-6 form-inline mb-2"}>
              <label className={"justify-content-end pr-0 " + (t.isFull ? 'col-sm-2' : 'col-sm-4')}>{t.label} :</label>
              <div className={t.isFull ? "col-sm-10" : "col-sm-8"}>
                {Render(t, this.onChange)}
              </div>
            </div>
          ))}
        </div>

        <div className="form-row">
          <div className="col-sm-12 form-inline mb-2">
            <label className="col-sm-2 justify-content-end pr-0">{remark.label} :</label>
            <div className="col-sm-10">
              {Render(remark, this.onChange)}
            </div>
          </div>
        </div>

        {/* Pit Parking */}
        <div className="col-sm-12 form-inline mb-2">
          <label className="col-sm-2 justify-content-end pr-0">{name && name.pitParking} :</label>
          <div className="col-sm-10">
            <Radio.Group
              name={'pitParking'}
              onChange={e => this.onChange({ name: 'pitParking', value: e.target.value })}
              value={data.pitParking}>
              <Radio value={1}>{name && name.unknown}</Radio>
              <Radio value={2}>{name && name.pit}</Radio>
              <Radio value={3}>{name && name.parking}</Radio>
            </Radio.Group>
          </div>
        </div>
        <div className="col-sm-12 form-inline mb-2">
          <label className="col-sm-2 justify-content-end"></label>
          {(data.pitParking === 2 || data.pitParking === 3) && <div className="col-sm-10 pl-0 pr-0">
            <Select
              name={'selectPitParking'}
              showSearch
              style={{ width: '100%' }}
              placeholder={name && name.selectPitParkingPlaceholder}
              optionFilterProp="children"
              onChange={value => this.onChange({ name: 'selectPitParking', value: value })}
              filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
              value={data.selectPitParking}>
              {dropdown.agOptions.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
            </Select>
          </div>}
        </div>

        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={this.props.onClose}
            className="btn btn-light mr-2">
            {name && name.cancel}
          </button>
          <button
            onClick={this.props.onClose}
            className="btn btn-success mr-2">
            {name && name.save}
          </button>
        </div>

      </Modal>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

export default connect(mapStateToProps)(ModalStatus)
