import React, { Component, Fragment } from 'react'
import {
  Table,
  Menu,
  Dropdown,
  message,
  Popconfirm,
} from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ModalForm from './ModalComponent'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import context from '../../utils/language'
import { getFlightStatById } from '../../redux/actions/flight'

class TableComponent extends Component {
  state = {
    visible: false,
    loadingSubmit: false,
    loadingSendback: false,
    loadingApprove: false,
    loadingCancel: false,
    dataModal: null,
    dataFlightStat: null,
    dataFilterAirport: null,
    isMultiApprove: false
  }

  showModal = data => {
    this.props.getFlightStatById(data.key)
    this.setState({ visible: true })
  }

  confirmShowApprove = (e, data) => {
    const { language } = this.props
    const name = context[language].searchTable
    message.success((name && name.flightNo) + ' ' + data.flightNo + ' ' + (name && name.approved))
  }

  confirmSendBack = (e, data) => {
    const { language } = this.props
    const name = context[language].searchTable
    message.warning((name && name.rejectFlight) + ' ' + data.flightNo + ' ' + (name && name.done))
  }

  onClose = () => {
    this.setState({ visible: false })
  }

  render() {
    const { flightStat, language } = this.props
    const { isMultiApprove } = this.state
    const name = context[language].searchTable
    const optionMenu = data => (
      <Menu>
        {data.status === 1 && <Menu.Item>
          <Popconfirm
            title={(name && name.approveFlight) + ' ' + data.flightNo}
            onConfirm={value => this.confirmShowApprove(value, data)}
            onCancel={this.cancelShow}
            okText={name && name.confirm}
            cancelText={name && name.cancel}>
            <Link
              to="#"
              onClick={e => {
                e.stopPropagation()
              }}>
              <FontAwesomeIcon icon="check" className="mr-2" />
              {name && name.approve}
            </Link>
          </Popconfirm>
        </Menu.Item>}

        {(data.status === 1 || data.status === 2) && <Menu.Item>
          <Popconfirm
            title={(name && name.reject) + ' ' + data.flightNo}
            onConfirm={value => this.confirmSendBack(value, data)}
            onCancel={this.cancelShow}
            placement="right"
            okText={name && name.confirm}
            cancelText={name && name.cancel}>
            <Link
              to="#"
              onClick={e => {
                e.stopPropagation()
              }}>
              <FontAwesomeIcon
                icon="level-down-alt"
                rotation={90}
                className="mr-2"
              />
              {name && name.reject}
            </Link>
          </Popconfirm>
        </Menu.Item>}

        <Menu.Item>
          <Link
            to="#"
            onClick={e => {
              this.showModal(data)
            }}>
            <FontAwesomeIcon icon="pen" className="mr-2" />
            {name && name.view}
          </Link>
        </Menu.Item>
      </Menu>
    )
    const columns = [
      {
        title: '',
        dataIndex: 'option',
        key: '1',
        render: (text, record) => (
          <Dropdown overlay={optionMenu(record)} trigger={['click']}>
            <Link className="ant-dropdown-link" to="#">
              <FontAwesomeIcon icon="ellipsis-v" />
            </Link>
          </Dropdown>
        ),
        fixed: 'left',
        width: 50
      },
      {
        title: 'Status',
        dataIndex: 'status',
        key: '2',
        fixed: 'left',
        width: 150,
        className: 'text-left',
        render: status => {
          switch (status) {
            case 1:
              return (
                <span>
                  <FontAwesomeIcon
                    icon={('fas', 'clock')}
                    className="text-warning"
                  />{' '}
                  {name && name.waiting}
                </span>
              )
            case 2:
              return (
                <span>
                  <FontAwesomeIcon
                    icon={('fas', 'check')}
                    className="text-success"
                  />{' '}
                  {name && name.approved}
                </span>
              )
            case 3:
              return (
                <span>
                  <FontAwesomeIcon
                    icon={('fas', 'exclamation-triangle')}
                    className="text-danger"
                  />{' '}
                  รอแก้ไข
                </span>
              )
            case 4:
              return (
                <span>
                  <FontAwesomeIcon
                    icon={('fas', 'file-alt')}
                    className="text-info"
                  />{' '}
                  บันทึกร่าง
                </span>
              )
            default: return null
          }
        }
      },
      { title: 'operator', dataIndex: 'operator', key: '3', width: 150 },
      { title: 'rec no.', dataIndex: 'recNo', key: '4', width: 150 },
      { title: 'trn date', dataIndex: 'trnDate', key: '5', width: 150 },
      { title: 'trn time', dataIndex: 'trnTime', key: '6', width: 150 },
      { title: 'airline', dataIndex: 'airline', key: '7', width: 150 },
      { title: 'flight no.', dataIndex: 'flightNo', key: '8', width: 150 },
      { title: 'callsign', dataIndex: 'callsign', key: '9', width: 150 },
      { title: 'air type', dataIndex: 'airType', key: '10', width: 150 },
      { title: 'traff type', dataIndex: 'traffType', key: '11', width: 150 },
      { title: 'port name icao', dataIndex: 'portICAO', key: '12', width: 150 },
      { title: 'port name iata', dataIndex: 'portIATA', key: '13', width: 150 },
      { title: 'arr dep', dataIndex: 'arrDep', key: '14', width: 150 },
      { title: 'city', dataIndex: 'city', key: '15', width: 150 },
      { title: 'city iata', dataIndex: 'cityIATA', key: '16', width: 150 },
      { title: 'card no.', dataIndex: 'cardNo', key: '17', width: 150 },
      {
        title: 'passenger',
        className: 'table-primary text-white',
        children: [
          { title: 'dom', dataIndex: 'passengerDom', key: '18', width: 150 },
          {
            title: 'inter',
            dataIndex: 'passengerInter',
            key: '19',
            width: 150
          },
          {
            title: 'infants',
            dataIndex: 'passengerInfants',
            key: '20',
            width: 150
          },
          { title: 'total', dataIndex: 'passengerTotal', key: '21', width: 150 }
        ]
      },
      {
        title: 'passenger',
        className: 'table-primary-light text-white',
        children: [
          {
            title: 'transfer',
            dataIndex: 'passengerTransfer',
            key: '22',
            width: 150
          },
          {
            title: 'transit',
            dataIndex: 'passengerTransit',
            key: '23',
            width: 150
          }
        ]
      },
      {
        title: 'freight (Kg.)',
        className: 'table-success text-white',
        children: [
          { title: 'dom', dataIndex: 'freightDom', key: '24', width: 150 },
          { title: 'inter', dataIndex: 'freightInter', key: '25', width: 150 },
          { title: 'total', dataIndex: 'freightTotal', key: '26', width: 150 }
        ]
      },
      {
        title: 'freight (Kg.)',
        className: 'table-success-light text-white',
        children: [
          {
            title: 'transfer',
            dataIndex: 'freightTransfer',
            key: '27',
            width: 150
          },
          {
            title: 'transit',
            dataIndex: 'freightTransit',
            key: '28',
            width: 150
          }
        ]
      },
      { title: 'mail (Kg.)', dataIndex: 'mail', key: '29', width: 150 },
      { title: 'remark', dataIndex: 'remark', key: '30', width: 150 }
    ]

    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          'selectedRows: ',
          selectedRows
        )
        this.setState({ isMultiApprove: selectedRows.length > 0 })
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User',
        name: record.name
      })
    }

    const data = []
    for (let run of flightStat) {
      data.push({
        key: run.flightStatsID,
        status: run.flightStatStatus,
        operator: run.operaterStat,
        recNo: run.recNo,
        trnDate: run.trnDate,
        trnTime: run.trnTime,
        airline: run.airline,
        flightNo: run.flightNo,
        callsign: run.callsign,
        airType: run.airType,
        traffType: run.traffType,
        portICAO: run.portNameICAO,
        portIATA: run.portNameIATA,
        arrDep: run.arr_dep,
        city: run.cityICAO,
        cityIATA: run.cityIATA,
        cardNo: run.cardNo,
        passengerDom: run.passenger_dom,
        passengerInter: run.passenger_inter,
        passengerInfants: run.passenger_infants,
        passengerTotal: run.passenger_total,
        passengerTransfer: run.passenger_transfer,
        passengerTransit: run.passenger_transit,
        freightDom: run.freight_dom,
        freightInter: run.freight_inter,
        freightTotal: run.freight_total,
        freightTransfer: run.freight_transfer,
        freightTransit: run.freight_transit,
        mail: run.freight_mail,
        remark: run.remark
      })

    }

    return (
      <Fragment>
        <div className="card card-body card-header-table">
          <div className="row">
            <div className="col">
              <div className="d-flex justify-content-center align-items-center">
                <button type="button" className="btn btn-primary">
                  <FontAwesomeIcon icon="chevron-left" />
                </button>
                <h5 className="mb-0 ml-5 mr-2 text-primary">สิงหาคม 2561</h5>
                <Link to="#" className="mr-5 text-success">
                  <FontAwesomeIcon icon="calendar-alt" />
                </Link>
                <button type="button" className="btn btn-primary">
                  <FontAwesomeIcon icon="chevron-right" />
                </button>
              </div>
            </div>
            <div className="col-auto">
              <button type="button" onClick={this.props.onToday} className="btn btn-outline-primary">
                {name && name.today}
              </button>
            </div>
          </div>
          <div className="row">
            {isMultiApprove && <div className="col-auto">
              <button type="button" className="btn btn-success mr-2">
                <FontAwesomeIcon icon="check" /> {name && name.approved}
              </button>
              <button type="button" className="btn btn-danger">
                <FontAwesomeIcon icon="reply" /> {name && name.reject}
              </button>
            </div>}
          </div>
        </div>
        <Table
          bordered={true}
          className="thead-uppercase tbody-center"
          rowSelection={rowSelection}
          pagination={{
            defaultPageSize: 10,
            position: 'bottom'
          }}
          columns={columns}
          dataSource={data}
          scroll={{ x: '300%', y: 400 }}
        />
        <ModalForm
          visible={this.state.visible}
          onClose={this.onClose}
        />
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
  flightStat: state.flightStat
})

const mapDispatchToProps = dispatch => ({
  getFlightStatById: (id) => dispatch(getFlightStatById(id)),
})

export default connect(mapStateToProps, mapDispatchToProps)(TableComponent)
