import React, { Component } from 'react'
import { Table } from 'antd'

class ModalDetailHistory extends Component {
  render() {
    const columns = [{
      title: 'Flight Status',
      dataIndex: 'flightStats',
      key: 'flightStats',
      width: 200
    }, {
      title: 'Time',
      dataIndex: 'flightStatusTime',
      key: 'flightStatusTime',
      width: 100
    }, {
      title: 'Cause',
      dataIndex: 'flightStatusReason',
      key: 'flightStatusReason',
      width: 500
    }, {
      title: 'Flight Remark',
      dataIndex: 'flightStatusRemark',
      key: 'flightStatusRemark',
      width: 500
    }]

    const data = [{
      key: '1',
      flightStats: 'Delayed',
      flightStatusTime: '13:50',
      flightStatusRemark: '-'
    }, {
      key: '2',
      flightStats: 'Confirmed',
      flightStatusTime: '13:55',
      flightStatusRemark: '-'
    }]

    return (
      <div className="table-responsive" style={{ backgroundColor: '#fff' }}>
        <Table columns={columns} dataSource={data} bordered={true} className="thead-uppercase tbody-center" />
      </div>
    );
  }
}

export default ModalDetailHistory