import React, { Component } from 'react'
import { Table } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'

class ResultTable extends Component {
  view = () => {
    const { history } = this.props
    history.push('/flightschedule/detail')
  }

  render() {
    const data = []
    for (let i = 0; i < 10; i++) {
      data.push({
        key: i,
        from: `ดอนเมือง`,
        to: `สุราษฏร์ธานี`,
        flightDate: `1 มี.ค. 2561 - 31 มี.ค. 2561`,
        flightCode: `A`,
        lastUpdate: `5 ม.ค. 2561`
      })
    }

    const columns = [
      {
        title: 'เส้นทาง',
        dataIndex: 'from',
        key: 'from',
        colSpan: 3
      },
      {
        colSpan: 0,
        render: () => (
          <span>
            <FontAwesomeIcon icon="exchange-alt" className="text-primary" />
          </span>
        )
      },
      {
        title: 'เส้นทาง',
        dataIndex: 'to',
        key: 'to',
        colSpan: 0
      },
      {
        title: 'ช่วงวันที่บิน',
        dataIndex: 'flightDate',
        key: 'flightDate'
      },
      {
        title: 'รหัสการบิน',
        dataIndex: 'flightCode',
        key: 'flightCode'
      },
      {
        title: 'วันที่แก้ไขล่าสุด',
        dataIndex: 'lastUpdate',
        key: 'lastUpdate'
      },
      {
        title: 'ดู',
        key: 'view',
        render: () => (
          <button onClick={this.view} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="file" />
          </button>
        )
      },
      {
        title: 'แก้ไข',
        key: 'edit',
        render: () => (
          <button type="button" className="btn btn-outline-warning btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }
    ]

    const table = <Table
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={data} />

    return (
      <div className="card card-body card-header-table">
        <div className="mb-3">
          <button type="button" className="btn btn-success">
            <FontAwesomeIcon icon="plus" /> เพิ่มแผนตารางการบิน
          </button>
        </div>
        {table}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(ResultTable)
