import React, { Component } from 'react'
import { Tabs, Table, Button } from 'antd'
import IconStatistics from '../../images/icons/icon-navbar-statistics.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './ResultTable.css'

const TabPane = Tabs.TabPane

class ResultTable extends Component {
  state = {
    index: '1'
  }

  onChangeTab = (key) => this.setState({ index: key })

  onUpdateStatus = item => {
    const { index } = this.state
    item.type = index === '1' ? 'arrival' : 'departure'
    this.props.onUpdateStatus(item)
  }

  render() {
    const { index } = this.state
    const bgColor = index === '1' ? 'bg-warning text-white' : 'bg-success text-white'
    const columns = [
      {
        title: 'Time', dataIndex: 'time', onHeaderCell: () => ({ className: bgColor })
      },
      { title: 'Flight No.', dataIndex: 'flightNo', onHeaderCell: () => ({ className: bgColor }) },
      { title: index === '1' ? 'From' : 'To', dataIndex: 'from', onHeaderCell: () => ({ className: bgColor }) },
      { title: 'Code share', dataIndex: 'codeShare', onHeaderCell: () => ({ className: bgColor }) },
      { title: 'Terminal', dataIndex: 'terminal', onHeaderCell: () => ({ className: bgColor }) },
      { title: 'Belt', dataIndex: 'belt', onHeaderCell: () => ({ className: bgColor }) },
      {
        title: 'LCD',
        dataIndex: 'lcd',
        render: lcd => <FontAwesomeIcon className={"fa-lg " + (lcd && 'text-primary')} icon={lcd ? 'eye' : 'eye-slash'} />,
        onHeaderCell: () => ({ className: bgColor })
      }, {
        title: 'Flight Status',
        render: item => (item.flightStatus || <Button onClick={() => this.onUpdateStatus(item)} type="primary">
          <img src={IconStatistics} width={18} className="mr-2" alt="IconStatistics" />
          Update Status</Button>),
        onHeaderCell: () => ({ className: bgColor })
      }, {
        title: 'Schedule',
        onHeaderCell: () => ({ className: bgColor }),
        render: () => <button className="btn btn-outline-warning btn-sm">
          <FontAwesomeIcon icon="pen" />
        </button>
      }, {
        title: 'Stat Data',
        onHeaderCell: () => ({ className: bgColor }),
        render: () => <button className="btn btn-outline-success btn-sm">
          <FontAwesomeIcon icon="plane" />
        </button>
      }
    ];

    const data = [{
      key: '1',
      time: '0.:55',
      flightNo: 'DD 7910',
      from: 'Don Mueng',
      codeShare: '',
      terminal: '1',
      belt: 1,
      lcd: true,
      flightStatus: 'Landed 03:55'
    }, {
      key: '2',
      time: '0.:55',
      flightNo: 'DD 7910',
      from: 'Don Mueng',
      codeShare: '',
      terminal: '1',
      belt: 1,
      lcd: false,
      flightStatus: null
    }, {
      key: '3',
      time: '0.:55',
      flightNo: 'DD 7910',
      from: 'Don Mueng',
      codeShare: '',
      terminal: '1',
      belt: 1,
      lcd: false,
      flightStatus: null
    }, {
      key: '4',
      time: '0.:55',
      flightNo: 'DD 7910',
      from: 'Don Mueng',
      codeShare: '',
      terminal: '1',
      belt: 1,
      lcd: false,
      flightStatus: null
    }, {
      key: '5',
      time: '0.:55',
      flightNo: 'DD 7910',
      from: 'Don Mueng',
      codeShare: '',
      terminal: '1',
      belt: 1,
      lcd: false,
      flightStatus: null
    }];

    const table = <Table
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={data} />

    return (
      <div className="card card-body card-header-table">
        <Tabs defaultActiveKey={index} onChange={this.onChangeTab} className="tabs-button">
          <TabPane tab={<span><FontAwesomeIcon icon="plane-arrival" /> Arrival</span>}
            key="1">
            {table}
          </TabPane>
          <TabPane tab={<span>Departure <FontAwesomeIcon icon="plane-departure" /></span>}
            key="2">
            {table}
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default ResultTable;