import React, { Fragment } from 'react'
import { HashRouter as Router, Route, Switch, withRouter  } from "react-router-dom"
import Home from './Home'
import Header from '../components/Header'
import Footer from '../components/Footer'
import Navbar from '../components/NavbarMenu'
import Statistics from './Statistics'
import FlightDetail from './FlightDetail'

import FlightSchedule from './FlightSchedule'
import FlightScheduleDetail from './FlightSchedule/FlightDetail'
import FlightCancel from './FlightSchedule/FlightCancel'

import OperationReport from './OperationReport'

const Nav = withRouter(Navbar)
const AppRouter = () => (
  <Router>
    <Fragment>
      <div className="container-fluid container-content">
        <Header />
        <Switch>
          <Route path="/" exact component={Home} />
          <Fragment>
            <Nav />
            <Route path="/statistics" component={Statistics} />
            <Route path="/flightdetail" component={FlightDetail} />
            <Route exact path="/flightschedule" component={FlightSchedule} />
            <Route exact path="/flightschedule/detail" component={FlightScheduleDetail} />
            <Route exact path="/flightschedule/detail/cancel" component={FlightCancel} />
            <Route path="/operationreport" component={OperationReport} />
          </Fragment>
        </Switch>
      </div>
      <Footer />
    </Fragment>
  </Router>
);

export default AppRouter