import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Divider, Button, Tabs, Icon } from 'antd'
import { Link } from 'react-router-dom'
import MultiDate from '../../components/MultiDate'
import TextArea from 'antd/lib/input/TextArea'
import { dayEn, monthEn } from '../../utils/date'
import { DateUtils } from 'react-day-picker'

const TabPane = Tabs.TabPane
const years = [2018, 2019]

class FlightCancel extends Component {
  state = {
    selectedDays: [
      new Date('2018-8-9'),
      new Date('2018-8-16'),
      new Date('2018-8-17'),
    ],
    all: [
      {
        date: new Date('2018-7-18'),
        remark: 'remark for this day...'
      }, {
        date: new Date('2018-7-19'),
        remark: 'remark for this day...'
      }
    ],
    year: 2018
  }

  renderHeader = () => {
    return <div className="card card-body bg-primary-light border-0 mb-2">
      <div className="col-md-12 text-primary mb-2">
        <FontAwesomeIcon icon="plane" /><span className="ml-2">Flight Detail</span>
      </div>
      <div className="container-fluid form-inline pl-0">
        <div className="col-md-8 form-inline pl-0">
          <div className="col-md-4">
            <div className="font-weight-bold">Don Mueang (DMK)</div>
            <div>08:00</div>
          </div>
          <div className="col-md-4 text-center text-primary">
            <FontAwesomeIcon icon="plane" />
            <div>Arrival</div>
          </div>
          <div className="col-md-4">
            <div className="font-weight-bold">Suratthani (URT)</div>
            <div>09:10</div>
          </div>

          <label className="col-lg-2 col-md-3 col-sm-4 justify-content-start mt-4">Date :</label>
          <label className="col-lg-10 col-md-9 col-sm-8  justify-content-start mb-0 mt-4">1 August 2018 - 31 August 2018</label>

          <label className="col-lg-2 col-md-3 col-sm-4 justify-content-end"></label>
          <label className="col-lg-10 col-md-9 col-sm-8  justify-content-start mb-2 mt-0">Monday, Tuesday, Friday</label>

        </div>
        <div className="col-md-4 form-inline">
          <label className="col-lg-6 col-md-7 col-sm-6 col-6 justify-content-end pl-0 mb-2">Flight No :</label>
          <label className="col-lg-6 col-md-5 col-sm-6 col-6 justify-content-start pl-0 mb-2">DD 9740</label>
          <label className="col-lg-6 col-md-7 col-sm-6 col-6 justify-content-end pl-0 mb-2">Airline :</label>
          <label className="col-lg-6 col-md-5 col-sm-6 col-6 justify-content-start pl-0 mb-2">Nok Air</label>
          <label className="col-lg-6 col-md-7 col-sm-6 col-6 justify-content-end pl-0 mb-2">Callsign :</label>
          <label className="col-lg-6 col-md-5 col-sm-6 col-6 justify-content-start pl-0 mb-2">B-HOL</label>
          <label className="col-lg-6 col-md-7 col-sm-6 col-6 justify-content-end pl-0 mb-2">Air Type :</label>
          <label className="col-lg-6 col-md-5 col-sm-6 col-6 justify-content-start pl-0 mb-2">B-747</label>
          <label className="col-lg-6 col-md-7 col-sm-6 col-6 justify-content-end pl-0 mb-2">Traff Type :</label>
          <label className="col-lg-6 col-md-5 col-sm-6 col-6 justify-content-start pl-0 mb-2">C - International Flight</label>
        </div>
      </div>
    </div>
  }

  onSelectDayChange = selectedDays => this.setState({ selectedDays })

  onRemove = day => {
    const { selectedDays } = this.state
    const selectedIndex = selectedDays.findIndex(selectedDay =>
      DateUtils.isSameDay(selectedDay, day)
    )
    if (selectedIndex > -1) {
      selectedDays.splice(selectedIndex, 1)
      this.setState({ selectedDays })
    }
  }

  renderList = (selected, all) => {
    let array = selected
    if (all) array = [...array, ...all.map(x => x.date)]
    this.monthTmp = null
    array.sort((a, b) => a.getTime() - b.getTime())
    return array.map((s, i) => {
      let selectedIndex = -1 // is selected tab
      if (all) {
        selectedIndex = all.findIndex(x =>
          DateUtils.isSameDay(x.date, s)
        )
      }
      const header = <div key={i}
        className={'col-md-12 form-inline pl-0 pr-0 font-weight-bold ' + (this.monthTmp === null ? '' : 'mt-2')}>
        {monthEn[s.getMonth()]}
      </div>
      const list = <div key={i + 1} className={'col-md-12 form-inline pl-0 pr-0'}>
        <span className='col-md-2 col-sm-2 col-2 pl-0 pr-0'>{dayEn[s.getDay()].substring(0, 3)}</span>
        <span className='col-md-5 col-sm-5 col-5 pl-0 pr-0'>
          <span className={'mr-2 ' + (s.getDate() < 10 ? 'ml-2' : 'ml-0')}>{s.getDate()}</span>
          {monthEn[s.getMonth()]}
        </span>
        <span className='col-md-4 col-sm-4 col-4 pl-0 pr-0 text-danger'>{selectedIndex === -1 && 'Selected'}</span>
        <span className='col-md-1 col-sm-1 col-1 pl-0 pr-0'>
          <Icon style={{ cursor: 'pointer' }} type="close" onClick={() => this.onRemove(s)} />
        </span>
      </div>
      const remark = selectedIndex > -1 && <div key={i + 2} className='col-md-12 form-inline pl-0 pr-0 mb-2'>
        <span className='col-md-2 col-sm-2 col-2 pl-0 pr-0'></span>
        <span className='col-md-10 col-sm-10 col-10 pl-0 pr-0'>
          {all[selectedIndex].remark}
        </span>
      </div>
      if (this.monthTmp === monthEn[s.getMonth()]) return [list, selectedIndex > -1 && remark]
      else {
        this.monthTmp = monthEn[s.getMonth()]
        return [header, list, selectedIndex > -1 && remark]
      }
    })
  }

  render() {
    const { selectedDays, year, all } = this.state
    const modifiers = [
      {
        month: 8,
        cancel: [new Date('2018-8-10')],
        normal: [new Date('2018-8-9'), new Date('2018-8-16'), new Date('2018-8-17')],
      }, {
        month: 9,
        cancel: [new Date('2018-9-10')],
        normal: [new Date('2018-9-9'), new Date('2018-9-16'), new Date('2018-9-17')],
      }, {
        month: 10,
        cancel: [new Date('2018-10-12')],
        normal: [new Date('2018-10-9'), new Date('2018-10-16'), new Date('2018-10-17')],
      }, {
        month: 11,
        cancel: [new Date('2018-11-16')],
        normal: [new Date('2018-11-9'), new Date('2018-11-16'), new Date('2018-11-17')],
      }, {
        month: 12,
        cancel: [new Date('2018-12-14')],
        normal: [new Date('2018-12-9'), new Date('2018-12-16'), new Date('2018-12-17')],
      }
    ]

    return (
      <div className="card card-body card-body-component">
        <div className="row mb-2">
          <div className="col-md-6">
            <Link to="/flightschedule/detail">
              <FontAwesomeIcon icon="angle-double-left" /><span className="ml-2">Back</span>
            </Link>
          </div>
          <div className="col-md-6 text-right text-primary">
            <FontAwesomeIcon icon="plane" /><span className="ml-2">Suratthani Airport</span>
          </div>
        </div>

        {this.renderHeader()}

        <div className="row mt-3">
          <div className="col-md-12">
            <FontAwesomeIcon icon="ban" className='text-danger' /><span className="ml-2">Cancel Flight</span>
          </div>
          <div className="col-md-12 mt-3">
            {years.map((y, i) => (
              <Button
                key={i}
                type={y === year ? "primary" : ''}
                onClick={() => this.setState({ year: y })}
                className={i === 0 ? '' : "ml-2"}>{y}</Button>
            ))}
          </div>
          <div className="col-md-12">
            <Divider />
          </div>
        </div>

        <div className="row mb-2 mt-0">
          <div className="col-md-12 text-right">
            <Button size='small' type="primary" style={{ width: 25 }} />
            <span className="align-middle ml-1">Flight Schedule</span>
            <Button size='small' className="bg-danger ml-2" style={{ width: 25 }} />
            <span className="align-middle ml-1">Flight Cancelled</span>
          </div>
        </div>

        <div className="row">
          <div className="col-xl-9 col-lg-9 col-md-12 form-inline align-items-start">
            {modifiers.map((m, i) => (
              <div key={i} className="col-xl-4 col-lg-5 col-md-5 col-sm-12 col-12 mb-2">
                <MultiDate
                  onChange={this.onSelectDayChange}
                  modifiers={m}
                  year={year}
                  selectedDays={selectedDays}
                  month={m.month} />
              </div>
            ))}
          </div>
          <div className="col-xl-3 col-lg-3 col-md-12">
            <div className="card card-body bg-light">
              <h5 className="text-center text-danger">List Of Flight Cancel</h5>
              <Tabs defaultActiveKey="1">
                <TabPane tab="Selected" key="1">
                  {this.renderList(selectedDays)}
                </TabPane>
                <TabPane tab="All" key="2">
                  {this.renderList(selectedDays, all)}
                </TabPane>
              </Tabs>

              {/* remark */}
              <div className='col-md-12 form-inline pl-0 pr-0 mt-4'>
                <span>Flight Remark :</span>
                <TextArea
                  placeholder={''}
                  rows="3"
                  style={{ width: '100%' }}
                />
              </div>

              {/* footer */}
              <div className="text-center mt-2">
                <Button className="ml-2">Cancel</Button>
                <Button type="primary" className="ml-2">Save</Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default FlightCancel