import React, { Component } from 'react'
import { Table } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class BillTable extends Component {
  state = {
    selectedRows: []
  }
  render() {
    const { selectedRows } = this.state
    const dataSource = [{
      key: '1',
      seq: 221,
      name: 'Air Transport Traffic at Domestic Airport'
    }, {
      key: '2',
      seq: 51,
      name: 'Monthly Scheduled Traffic for Year Operated by international Airport'
    }, {
      key: '3',
      seq: 224,
      name: 'Traffic of Commercial and Non Commercial Flight Classified By Airport / Month'
    }, {
      key: '4',
      seq: 198,
      name: 'Commercial Air Transport Statistics Classified By Airline Scheduled Flight (All Card)'
    }, {
      key: '5',
      seq: 19,
      name: 'Commercial Air Transport Statistics Classified By City Scheduled Flight (All Card)'
    }];

    const columns = [{
      title: 'Report',
      dataIndex: 'name',
      className: 'text-left',
    }, {
      title: '',
      dataIndex: '',
      key: 'x',
      width: 150,
      render: () => <div className="form-inline d-flex justify-content-between">
        <button type="button" className="btn btn-outline-warning btn-sm">
          <FontAwesomeIcon icon="pen" />
        </button>
        <button className="btn btn-outline-success btn-sm ml-2">
          <FontAwesomeIcon icon="download" />
        </button>
      </div>
    }]

    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({ selectedRows })
      }
    }
    return (
      <div className="card card-body card-header-table form-inling text-primary">
        <div className="mb-2">
          <FontAwesomeIcon icon="signal" /><span className="ml-2">Bill</span>
        </div>

        <Table
          rowSelection={rowSelection}
          className="tbody-center bg-white"
          dataSource={dataSource}
          pagination={false}
          footer={() => {
            if (selectedRows.length === 0) return false
            return <button className="btn btn-outline-success btn-sm">
              <FontAwesomeIcon icon="download" />
              <span className="ml-2">Export</span>
            </button>
          }}
          columns={columns} />

      </div>
    )
  }
}

export default BillTable