import React, { Component, Fragment } from 'react'
import Search from '../../components/Search'
import context from '../../utils/language'
import { connect } from 'react-redux'
import CustomDropdown from '../../components/Dropdown'
import moment from 'moment'
import { TimePicker, Checkbox } from 'antd'
import ResultTable from './ResultTable'
import ModalStatus from './ModalStatus'

const timeFormat = 'HH:mm'
class FlightDetail extends Component {
  state = {
    form: {},
    isOpenModal: false,
    selectedFlight: {}
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  onReset = () => this.setState({ form: {} })

  onSearch = () => {
    // use form data to search
  }

  onUpdateStatus = item => {
    this.setState({ isOpenModal: true, selectedFlight: item })
  }

  render() {
    const { language, dropdown } = this.props
    const { form, isOpenModal, selectedFlight } = this.state
    const name = context[language].viewStatModal
    const optionsAirport = dropdown.airPortOptions.map(x => {
      x.id = x.airportID
      x.text = x.airportName
      return x
    })
    const optionsAirline = dropdown.airlineOptions.map(x => {
      x.id = x.airlineID
      x.text = x.showFilter
      return x
    })
    const optionsAirtype = dropdown.airTypeOptions.map(x => {
      x.id = x.airTypeID
      x.text = x.airType
      return x
    })

    const searchForm = {
      normal: [
        {
          type: 'custom',
          label: 'Airports',
          component: <CustomDropdown
            selected={form.airportID}
            onSelect={item => {
              form.airportID = item.airportID
              this.setState({ form })
            }}
            placeholder={'Select'}
            options={optionsAirport} />
        }, {
          type: 'datepicker',
          name: 'date',
          label: 'Date',
          value: form.date,
          placeholder: name && name.date,
        }, {
          type: 'custom',
          label: 'Airline',
          component: <CustomDropdown
            selected={form.airlineID}
            onSelect={item => {
              form.airlineID = item.airlineID
              this.setState({ form })
            }}
            placeholder={'Select'}
            options={optionsAirline} />
        }, {
          type: 'custom',
          label: 'Flight No',
          name: 'flightNo',
          component: <CustomDropdown
            selected={form.airportID}
            onSelect={item => {
              form.airportID = item.Flight
              this.setState({ form })
            }}
            placeholder={'Select'}
            options={[]} />
        }
      ],
      advance: [
        {
          type: 'select',
          name: 'traffType',
          label: name && name.traffType,
          value: form.traffType,
          placeholder: name && name.traffTypePlaceholder,
          options: dropdown.agOptions
        }, {
          type: 'select',
          name: 'fromTo',
          mode: 'multiple',
          label: 'From / To',
          value: form.fromTo,
          placeholder: name && name.fromToPlaceholder,
          options: dropdown.traffTypeOptions
        }, {
          type: 'custom',
          label: 'Flight No',
          name: 'flightNo',
          component: <div className="form-row">
            <div className="col">
              <TimePicker
                name="startTime"
                onChange={(value, dateString) => {
                  form.startTime = dateString
                  this.setState({ form })
                }}
                defaultOpenValue={moment(form.startTime || '00:00', timeFormat)}
                format={timeFormat}
                placeholder={name && name.betweenDatePlaceholder}
                style={{ width: '100%' }}
              />
            </div>
            <div className="col-auto align-self-center">{' '}-{' '}</div>
            <div className="col">
              <TimePicker
                name="endTime"
                onChange={(value, dateString) => {
                  form.endTime = dateString
                  this.setState({ form })
                }}
                defaultOpenValue={moment(form.endTime || '00:00', timeFormat)}
                format={timeFormat}
                placeholder={name && name.betweenTimePlaceholder}
                style={{ width: '100%' }}
              />
            </div>
          </div>
        }, {
          type: 'select',
          name: 'callsign',
          label: 'Callsign',
          value: form.callsign,
          placeholder: name && name.callsignPlaceholder,
          options: dropdown.traffTypeOptions
        }, {
          type: 'custom',
          label: 'Air Type',
          name: 'airTypeID',
          component: <CustomDropdown
            selected={form.airTypeID}
            onSelect={item => {
              form.airTypeID = item.airTypeID
              this.setState({ form })
            }}
            placeholder={'Select'}
            options={optionsAirtype} />
        }, {
          type: 'custom',
          label: 'Flight Status',
          fullRow: true,
          component: <Fragment>
            <Checkbox
              name="checkInOutAll"
              indeterminate={form.status && form.status.length < 5 && form.status.length > 0}
              onChange={e => {
                form.status = e.target.checked ? ['1', '2', '3', '4', '5'] : []
                this.setState({ form })
              }}
              checked={form.status && form.status.length === 5}
              className="d-inline-block mr-2 border-right">All Status</Checkbox>
            <Checkbox.Group
              name="checkInOut"
              options={[
                { label: 'No Status', value: '1' },
                { label: 'Canceled', value: '2' },
                { label: 'Delayed', value: '3' },
                { label: 'Confirmed', value: '4' },
                { label: 'Landed', value: '5' },
              ]}
              onChange={checkedList => {
                form.status = checkedList
                this.setState({ form })
              }}
              value={form.status || []} />
          </Fragment>
        }, {
          type: 'input',
          name: 'terminal',
          label: 'Terminal',
          value: form.terminal,
          placeholder: name && name.terminalPlaceholder,
        }, {
          type: 'input',
          name: 'belt',
          label: 'Belt',
          value: form.belt,
          placeholder: name && name.beltPlaceholder,
        }
      ]
    }
    return (
      <div className="card card-body card-body-component">
        <Search
          template={searchForm}
          onSearch={this.onSearch}
          onReset={this.onReset}
          onChange={this.onChange} />

        <ResultTable onUpdateStatus={this.onUpdateStatus} />

        <ModalStatus
          flight={selectedFlight}
          open={isOpenModal}
          selectedFlight={selectedFlight}
          onClose={() => this.setState({ isOpenModal: false })}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

export default connect(mapStateToProps)(FlightDetail)
