const traffType = []
for (let i = 10; i < 36; i++) {
  traffType.push({ value: i.toString(36) + i, text: i.toString(36) + i })
}

const initState = {
  airPortOptions: [],
  airlineOptions: [],
  airTypeOptions: [],
  flightStatusOptions: [],
  traffTypeOptions: traffType,
  agOptions: [
    { value: 'A', text: 'A' },
    { value: 'B', text: 'B' },
    { value: 'C', text: 'C' },
    { value: 'D', text: 'D' },
    { value: 'E', text: 'E' },
    { value: 'F', text: 'F' },
    { value: 'G', text: 'G' }
  ]
}
export default (state = initState, action) => {
  switch (action.type) {
    case 'SET_DROPDOWN_OPTIONS':
      state[action.key] = action.data
      return { ...state }
    default:
      return state
  }
}
